<template>
    <div class="fixedUiElements">
        <div class="audioFiles">
            <audio ref="strikeAudio" :src="strikeAudio" preload="auto"></audio>
            <audio ref="strikeAudio2" :src="strikeAudio2" preload="auto"></audio>
            <audio ref="longStrike" :src="longStrike" preload="auto"></audio>
            <audio ref="winner" :src="winner" preload="auto"></audio>
            <audio ref="losser" :src="losser" preload="auto"></audio>
        </div>
        <div class="developerName">
            By <a href="https://www.facebook.com/Baji321" target="_blank">Baji</a> with
            <unicon name="heart" />
        </div>
        <unicon class="helpIcon" @click="toggleHowToPlayModal()" name="question-circle" />
        <unicon class="soundSwitchIcon" @click="switchSound()" :name="isSoundOn?'volume':'volume-mute'" />
        <b-modal size="lg" v-model="howToPlayModal" title="How To Play" ok-only>
            <ul class="howToPlayUl">
                <li>The rules of this game are quite simple.</li>
                <li>Each player will be having a board of 25 squares having numbers 1 to 25.</li>
                <li>The tricky part is that each player's numbers are randomly ordered.</li>
                <li>When the turn comes player should strike off any non striked number, this will be striked in all the other player's board.</li>
                <li>Players objective is to make 5 consecutive squares striked (vertically, horizontally and diagonally) making a long strike</li>
                <li>The first player to get 5 long strikes wins the game.</li>
            </ul>
            <div class="howToImage">
                <img :src="howToPlayImg">
            </div>
        </b-modal>
    </div>
</template>

<script>
import strikeAudio from '../assets/sounds/penStrike.mp3'
import strikeAudio2 from '../assets/sounds/penStrike2.mp3'
import longStrike from '../assets/sounds/longStrike.mp3'
import winner from '../assets/sounds/winner.mp3'
import losser from '../assets/sounds/losser.mp3'
import store from '@/store'
export default {
    data:function(){
        return{
            howToPlayModal:false,
            strikeAudio,
            strikeAudio2,
            longStrike,
            winner,
            losser
        }
    },
    computed:{
        isSoundOn:()=>store.state.sound.isSoundOn,
        howToPlayImg () {
            return require('../assets/images/HowToPlay.jpg')
        }
    },
    methods:{
        toggleHowToPlayModal(){
            this.howToPlayModal = !this.howToPlayModal;
        },switchSound(){
            store.dispatch('switchSound',!this.isSoundOn);
        }
    },mounted:function() {
        store.state.sounds = this.$refs;
        store.state.sound.soundElements = this.$refs;
    }
}
</script>